const resultDescriptions = {
  okay:       "沒關係，先把牆上的知識\n仔細看過一遍，再回來重新挑戰！",
  nice:       "有潛力～再仔細了解更多\n野外調查知識，你會表現更棒喔！",
  great:      "很棒喔！再加把勁研究一番，\n你一定會成為優秀的調查家！",
  excellent:  "你就是野外調查小專家！\n快教教其他人野外調查是什麼吧！"
};

export default resultDescriptions;


